export default function (props, ref, key) {
    return (
        <skoash.Screen
            {...props}
            ref={ref}
            key={key}
            id="cards"
        >
            <skoash.Image
                className="hidden"
                src={MEDIA.SPRITE + 'cards.1.png'}
            />
            <skoash.Image
                className="hidden"
                src={MEDIA.SPRITE + 'cards.2.png'}
            />
            <skoash.Image
                className="hidden"
                src={MEDIA.SPRITE + 'cards.3.png'}
            />
            <skoash.Image
                className="hidden"
                src={MEDIA.SPRITE + 'cards.4.png'}
            />
            <skoash.Image
                className="hidden"
                src={MEDIA.IMAGE + 'card.png'}
            />
            <skoash.Image
                className="hidden"
                src={MEDIA.IMAGE + 'cards.trash.png'}
            />
            <skoash.MediaCollection
                play={_.get(props, 'data.selectable.target.props.message')}
            >
              <skoash.Audio
                  type="voiceOver"
                  ref="reduce"
                  src={MEDIA.VO + 'ReduceStrong.mp3'}
              />
              <skoash.Audio
                  type="voiceOver"
                  ref="reuse"
                  src={MEDIA.VO + 'ReuseStrong.mp3'}
              />
              <skoash.Audio
                  type="voiceOver"
                  ref="recycle"
                  src={MEDIA.VO + 'RecycleStrong.mp3'}
              />
            </skoash.MediaCollection>
            <skoash.Selectable
                dataTarget="selectable"
                selectClass="HIGHLIGHTED"
                list={[
                    <skoash.Component type="li" correct={true} message="reduce">
                        <div className="side b"><div/></div>
                        <div className="side a"><div/></div>
                    </skoash.Component>,
                    <skoash.Component type="li" correct={true} message="reuse">
                        <div className="side b"><div/></div>
                        <div className="side a"><div/></div>
                    </skoash.Component>,
                    <skoash.Component type="li" correct={true} message="recycle">
                        <div className="side b"><div/></div>
                        <div className="side a"><div/></div>
                    </skoash.Component>,
                ]}
            />
        </skoash.Screen>
    );
}



